<template>
	<view class="wrapper">

		<view class="header">
			<view class="btn menu">
				<image src="/static/images/menu.png" mode="heightFix" @click="showSlide"></image>
			</view>
			<view class="title">
				AiChat
			</view>
			<view class="btn edit" @click="deleteList()">
				<image src="/static/images/delete.svg" mode="heightFix"></image>
			</view>
		</view>

		<view class="content">
			<!-- 聊天列表 -->
			<scroll-view :style="{ height: 'calc(100vh - 254px)' }" scroll-y :scroll-top="scrollTop"
				:scroll-with-animation="true" v-if="chatList.length > 0">

				<view v-for="(item,index) in chatList" :key="index" class="content-item">
					<!-- 问题 -->
					<view class="item question" v-if="item.role == 'user'">
						<view class="itemWrap">
							<view class="left">
								<image src="/static/images/userAvatar.svg" class="avatar"></image>
							</view>
							<view class="right">
								<view class="name">
									You
								</view>
								<view class="textWrap">
									<input type="text" v-model="editQuestion" v-if="edit == index">
									<view class="text" v-else>
										<!-- {{ item.question }} -->
										<wemark :md="item.msg" link highlight type="wemark"></wemark>
									</view>
								</view>

								<!-- <view class="toolWrap">
									<view class="tool" v-if="tool == index"
										style="display: flex;justify-content: center;margin-top: 8px;">
										<button style="margin: 0;margin-right: 8px;" type="primary" size="mini"
											@click="confirmEdit(index)">提交</button>
										<button style="margin: 0;" type="default" size="mini"
											@click="cancelEdit(index)">取消</button>
									</view>
									<view class="tool" v-else>
										<image src="/static/images/edit.png" @click="editText(index)"></image>
									</view>

								</view> -->

							</view>
						</view>
					</view>
					<!-- 答案-有答案的时候  -->
					<view class="item answer" v-if="item.msg && item.role == 'assistant'">
						<view class="itemWrap">
							<view class="left">
								<image src="/static/images/ai_line.svg" class="avatar"></image>
							</view>
							<view class="right">
								<view class="name">
									AiChat
								</view>
								<view class="text">
									<!-- {{ item.answer }} -->
									<wemark :md="item.msg" link highlight type="wemark"></wemark>
								</view>
								<view class="tool">
									<image src="/static/images/copy.png" @click="copy(item.msg)"></image>
								</view>
							</view>
						</view>
					</view>
					<!-- 答案-还未回复答案的时候 -->
					<view class="item answer" v-if="!item.msg">
						<view class="itemWrap">
							<view class="left">
								<image src="/static/images/ai_line.svg" class="avatar"></image>
							</view>
							<view class="right">
								<view class="name">
									AiChat
								</view>
								<view class="text" style="display: flex;">
									<uni-load-more iconType="circle" :status="status" />
								</view>
							</view>
						</view>
					</view>

				</view>


			</scroll-view>

			<view class="placeholder" v-else>
				<view class="logoWrap">
					<image src="/static/images/ai_line.svg" mode="widthFix" class="logo"></image>
				</view>
				<view class="placeholderTitle">
					How can I help you today?
				</view>

			</view>
		</view>

		<view class="footer">
			<view class="footerWrapper">
				<uni-file-picker ref="upload" auto-upload="false" limit="1" file-mediatype="all" @select="select" ></uni-file-picker>
				<view class="searchBox">
					<input type="text" class="search" placeholder="搜索您需要的内容" v-model="question"
						@confirm="handleConfirm()" />
					<image src="/static/images/send-2.svg" class="send" v-if="question.length > 0 && ifComplete"
						@click="ask()">
					</image>
					<image src="/static/images/loadingv1.svg" class="send" v-else-if="question.length > 0 && !ifComplete">
					<image src="/static/images/send-1.svg" class="send" v-else></image>
				</view>
				<view class="tips">
					AiChat can make mistakes.
				</view>
			</view>
		</view>

		<!-- <uni-popup ref="alertDialog" type="dialog">
			<uni-popup-dialog ref="upload" type="info" confirmText="确定" title="通知" :content="dialogText" @confirm="dialogConfirm"
				@close="dialogClose">
			</uni-popup-dialog>
		</uni-popup> -->
	</view>
</template>

<script>
	import wemark from '@/static/wemark-master/wemark/wemark.js'

	const App = getApp();
	export default {
		components: {
			wemark
		},
		data() {
			return {
				baseUrl: 'https://www.scdpf.xyz',
				basev1Url: 'https://www.scdpf.xyz',
				// baseUrl: 'https://www.dengpengfei.xyz',
				// basev1Url: 'http://8.219.80.247:39001',
				// basev1Url: 'http://192.168.71.150:5026',
				// baseUrl: 'https://8.219.80.247:39000',
				chatList: [], // 聊天记录列表
				imageValue: [], // 文件列表
				fileUrl: '',
				fileId: '',
				email: '', // 邮箱
				question: '', // 当前问题
				groupId: '', //  当前对话组 id
				groupName: '', // 当前对话组名称
				groupList: {
					earlier: [],
					last_30_days: [],
					last_7_days: [],
					today: []
				}, //  聊天组列表
				scrollTop: 0, // 聊天列表滚动条距离顶部距离
				status: 'loading', // 加载组件状态
				edit: null, // 编辑问题 index
				tool: null, // 编辑问题组工具的index
				editQuestion: '', // 编辑问题的内容
				more: false,
				slideVisible: false,
				ifChange: false,
				ifComplete: true,
				dialogText: '剩余询问次数：0 次！请充值后继续使用',
			    timer: null, // 用于存储定时器的引用
			};
		},
		onShow() {
			this.setTabBarIndex(1);
			this.email = App.globalData.unionID;
			// this.getList();
			this.groupId = uni.getStorageSync('groupId');
			console.log('聊天组 id 为', this.groupId);
			this.getChatHistory()

		},
		methods: {
			deleteList() {
				const that = this;
				uni.request({
					url: that.basev1Url + '/delete_list',
					method: "POST",
					data:{
						email: that.email
					},
					success(res) {
						console.log(res.data);
						if(res.data.code = 200) {
							uni.showToast({
								title: ' 删除线程对话成功',
								duration: 1000 //单位毫秒
							});
							that.getChatHistory()
						}
					}
				})
			},
			select(e) {
				const that = this;
				const tempFilePaths = e.tempFilePaths;
				const imgUrl = tempFilePaths[0]
				that.fileUrl = imgUrl;
				that.ifComplete = false;
				
				uni.uploadFile({
					url: that.basev1Url + '/upload_file',
					filePath: that.fileUrl,
					name: 'file',
					header: {
						"Content-Type": "multipart/form-data"
					},
					timeout: 1000000, // 设置超时时间，单位为毫秒
					success: (res) => {
						const ans = JSON.parse(res.data)
						that.fileId = ans.file_id;
						that.pageScrollTo();
						that.ifComplete = true;
					},
					fail: function(res) {
						console.log('upload fail', res);
						that.ifComplete = false;
					}
				});
			},
			handleConfirm() {
				if (this.question.length > 0 && this.ifComplete) {
					this.ask();
				}
			},
			
			ask() {
			  const that = this;
			
			  // 判断是否请求问题为空
			  if (that.question.length === 0) {
			    return;
			  }
			
			  // 添加用户问题到聊天列表
			  that.chatList.push({
			    msg: that.question,
			    role: 'user'
			  }, {
			    msg: '',
			    role: 'assistant'
			  });
			
			  that.pageScrollTo();
			
			  // 定义共用的请求成功处理函数
			  function handleRequestSuccess(res) {
			    that.chatList = res.data.qa_pairs;
			    that.pageScrollTo();
			    // clearInterval(that.timer);
			    // that.timer = null;
			  }
			
			  // 发起定时请求
			  // that.timer = setInterval(() => {
			  //   uni.request({
			  //     url: that.basev1Url + '/get_list',
			  //     method: 'POST',
			  //     data: {
			  //       email: that.email
			  //     },
			  //     success(res) {
			  //       if (res.data.qa_pairs.length > that.chatList.length) {
			  //         that.chatList = res.data.qa_pairs;
			  //         that.pageScrollTo();
			  //         that.chatList.push({
			  //           msg: '',
			  //           role: 'assistant'
			  //         });
			  //       }
			  //     }
			  //   });
			  // }, 5000);
			
			  // 发起分析请求
			  const requestData = {
			    email: that.email,
			    question: that.question
			  };
			
			  if (that.fileId) {
			    requestData.file_id = that.fileId;
			  }
			
			  uni.request({
			    url: that.basev1Url + '/analyze_esg_report',
			    method: "POST",
			    data: requestData,
				timeout: 1000000, // 设置超时时间，单位为毫秒
			    success(res) {
			      handleRequestSuccess(res);
			    }
			  });
			
			  // 清空上传的文件和重置相关变量
			  that.$refs.upload.clearFiles();
			  that.question = '';
			  that.imageValue = [];
			  that.fileId = '';
			},

			getList() {
				// 获取当前用户的所有对话组列表
				const that = this;
				uni.request({
					url: that.baseUrl + '/get_chat_groups',
					method: 'POST',
					data: {
						email: that.email
					},
					success(res) {
						if (res.data.code == 200) {
							that.groupList = res.data.chat_groups;
							console.log('列表为聊天组', that.groupList);
						}
					}
				})
			},
			getChatHistory() {
				// 获取当前分组的历史对话记录
				const that = this;
				uni.request({
					url: that.basev1Url + '/get_list',
					method: 'POST',
					data: {
						email: that.email
					},
					success(res) {
						that.chatList = res.data.qa_pairs;
						that.pageScrollTo()
						console.log(that.chatList);
					}
				})
			},
			addNewChat(chatName) {
				// 新建对话组
				const that = this;
				uni.request({
					url: that.baseUrl + '/add_chat_groups',
					method: 'POST',
					data: {
						email: that.email,
						chat_name: chatName
					},
					success(res) {
						if (res.data.code == 200) {
							that.groupId = res.data.chat_id;
							uni.setStorageSync('groupId', res.data.chat_id);
							that.getChatHistory();
							that.hideSlide()
							uni.showToast({
								title: '新建对话成功',
								duration: 1000 //单位毫秒
							});

						}
					}
				})
			},
			delChat(index) {
				// 删除对应分组对话
				const that = this;
				uni.request({
					url: that.baseUrl + '/ask/delete_history',
					method: 'POST',
					data: {
						email: that.email,
						group_id: index
					},
					success(res) {
						if (res.data.code == 200) {
							that.groupId = '';
							uni.setStorageSync('groupId', '');
							that.chatList = [];
							console.log('删除对话成功');
							that.hideSlide();
							uni.showToast({
								title: '删除对话成功',
								duration: 1000 //单位毫秒
							});
							that.getChatHistory()
						}
					}
				})
			},
			changeName(name) {
				const that = this;
				uni.request({
					url: that.baseUrl + '/change_chat_group_name',
					method: 'POST',
					data: {
						email: that.email,
						group_id: that.groupId,
						group_name: name
					},
					success(res) {
						if (res.data.code == 200) {
							console.log('修改成功');
							that.hideSlide();
							that.ifChange = false;
							uni.showToast({
								title: '修改成功',
								duration: 1000 //单位毫秒
							});
						}
					}
				})
			},
			rename(name) {
				this.ifChange = !this.ifChange;
				this.groupName = name;

			},
			pageScrollTo() {
				// 滚动条滚到最底部
				this.$nextTick(() => {
					uni.createSelectorQuery()
						.in(this)
						.selectAll('.content-item')
						.boundingClientRect((rects) => {
							if (rects.length > 0) {
								const totalHeight = rects.reduce((acc, rect) => acc + rect.height, 0);
								// 将 scroll-top 设置为内容的高度减去 scroll-view 的高度
								this.scrollTop = totalHeight - 300; // 假设 scroll-view 的高度为 300rpx
							}
						})
						.exec();
				});
			},
			copy(value) {
				// 复制内容
				uni.setClipboardData({
					data: value + '', // 这里是个坑接受字符串类型 value转化为字符串
					success: function() {
						//调用方法成功
						console.log('success');
						uni.showToast({
							title: '复制成功',
							duration: 1000 //单位毫秒
						});
					}
				})
			},
			editText(index) {
				// 点击编辑按钮，进入编辑模式
				this.edit = index;
				this.tool = index;
				// 将点击的题目赋值给输入框的值
				this.editQuestion = this.chatList[index].question;
			},
			confirmEdit(index) {
				// 点击确认编辑按钮，发送请求
				const that = this;
				this.edit = null;
				this.tool = null;

				const handleChatRequest = () => {
					that.chatList.push({
						question: that.editQuestion,
						answer: ''
					});
					that.pageScrollTo();

					that.ifComplete = false

					const requestTask = uni.request({
						url: that.baseUrl + '/ask',
						method: 'POST',
						enableChunked: true, //配置这里
						data: {
							email: that.email,
							question: that.editQuestion,
							group_id: that.groupId
						},
						success(res) {
							that.ifComplete = true;
						},
						fail(error) {
							uni.showToast({
								title: '请求失败',
								icon: 'fail',
								duration: 1000 //单位毫秒
							});
							that.getChatHistory();
						}
					})
					that.editQuestion = '';
					requestTask.onHeadersReceived(function(res) {
						console.log(res.header);
					});
					// 这里监听消息
					requestTask.onChunkReceived(function(res) {
						let text = Buffer.from(res.data).toString('utf-8');;
						that.chatList[that.chatList.length - 1].answer += text;
						that.pageScrollTo();
					})
				};

				handleChatRequest();
			},
			cancelEdit(index) {
				// 点击取消编辑按钮
				this.edit = null;
				this.tool = null;
				console.log(this.chatList[index].question, '取消点击编辑的内容为');
			},
			showSlide() {
				this.slideVisible = true;
				this.getList();
			},
			hideSlide() {
				this.slideVisible = false;
				this.more = false;
			},
			toggleGroup(index) {
				uni.setStorageSync('groupId', index);
				this.groupId = index;
				this.hideSlide();
				this.getChatHistory();
			},
			open() {
				this.$refs.alertDialog.open()
			},
			dialogConfirm() {
				console.log('点击确认')
				uni.reLaunch({
					url: "/pages/tabBar/my/my"
				});
			},
			dialogClose() {
				console.log('点击关闭')
				uni.reLaunch({
					url: "/pages/tabBar/my/my"
				});
			},
		}

	}
</script>

<style lang="scss">
	.wemark_inline_text {
		font-size: 16px !important;
		line-height: 24px !important;
	}

	.wrapper {
		height: 100vh;

		.header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #D9D9D9;

			.btn {
				image {
					width: 18px;
					height: 18px;
					padding: 13px 12px;
				}
			}
		}

		.content {
			margin-top: 8px;
			// overflow: hidden;
			// height: calc(100vh - 220px);

			.placeholder {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				height: calc(100vh - 220px);

				.placeholderTitle {
					font-size: 24px;
					line-height: 32px;
					font-weight: 500;
					color: #000000;
					margin-top: 12px;
				}

				.logoWrap {
					width: 71px;
					height: 71px;
					border-radius: 50px;
					border: 1px solid #E5E5E5;
					box-sizing: border-box;
					display: flex;
					align-items: center;
					justify-content: center;

					.logo {
						width: 100%;
						height: auto;
					}
				}

			}

			.item {
				.itemWrap {
					padding: 8px 16px;
					width: 100%;
					display: flex;
					box-sizing: border-box;

					.left {
						box-sizing: border-box;
						padding-top: 2px;
						width: 24px;
						height: 24px;
						border-radius: 50px;
						border: 1px solid #000;
						display: flex;
						align-items: center;
						justify-content: center;
						overflow: hidden;
						margin-right: 12px;

						.avatar {
							width: 30px;
							height: 30px;
						}
					}

					.right {
						display: flex;
						flex-direction: column;
						flex: 1;
						width: 80%;


						.name {
							font-size: 16px;
							line-height: 24px;
							font-weight: 500;
						}

						.text {
							font-size: 16px;
							line-height: 24px;
						}

						.tool {
							display: flex;
							align-items: center;

							image {
								width: 18px;
								height: 18px;
								padding: 4px 0;
								padding-right: 4px;
							}
						}
					}
				}

			}
		}

		.footer {
			position: fixed;
			bottom: 80px;
			left: 0;
			right: 0;
			background-color: #ffffff;
			z-index: 99;

			.footerWrapper {
				display: flex;
				flex-direction: column;
				align-items: center;
				padding: 0 8px;

				.searchBox {
					width: 100%;
					border-radius: 16px;
					border: 1px solid #979797;
					position: relative;
					height: 44px;
					box-sizing: border-box;

					.search {
						padding: 10px 12px;
						width: 80%;
						box-sizing: border-box;
						height: 100%;

						&::placeholder-shown {
							color: #898999;
							font-size: 16px;
							line-height: 24px;
						}
					}

					.send {
						width: 30px;
						height: 30px;
						position: absolute;
						top: 7px;
						right: 12px;
						z-index: 999;
					}
				}

				.tips {
					font-size: 12px;
					line-height: 16px;
					color: #898999;
					margin: 8px auto;
				}
			}
		}

		.slide {
			transition: transform 0.3s ease-out;
			z-index: 99999;
			position: fixed;
			top: 0;
			bottom: 0;
			left: 0;

			height: 100%;
			padding: 14px 12px;
			box-sizing: border-box;
			background-color: #000;
			min-width: 70%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			.slideHeader {
				height: 40px;
				width: 100%;
				// background-color: #D9D9D9;
				padding: 6px 8px;
				box-sizing: border-box;
				display: flex;
				align-items: center;
				justify-content: space-between;

				.left {
					display: flex;
					align-items: center;

					.avatarWrap {
						width: 28px;
						height: 28px;
						background-color: #ffffff;
						border-radius: 50px;
						margin-right: 8px;
						overflow: hidden;
						display: flex;
						justify-content: center;
						align-items: center;

						.avatar {
							width: 100%;
							height: auto;
						}
					}

					.headerText {
						font-size: 14px;
						line-height: 20px;
						color: #ffffff;
						font-weight: 500;
					}
				}

				.addChat {
					width: 18px;
					height: 18px;
				}
			}

			.placeholder {
				font-size: 18px;
				color: #fff;
				text-align: center;
				display: flex;
				align-items: center;
				justify-content: center;
			}

			.slideList {
				// background-color: aqua;

				.itemGroup {
					position: relative;
					padding-top: 20px;
					box-sizing: border-box;

					.date {
						box-sizing: border-box;
						padding-top: 12px;
						padding-left: 8px;
						padding-bottom: 8px;
						padding-right: 8px;
						font-size: 12px;
						line-height: 16px;
						font-weight: 500;
						color: #666666;
					}

					.item {
